.price {
    background: url('https://nplus1.ru/images/2016/11/17/7b4da32d4f1ecb43fa0523baf6e7ecf9.jpg')
        no-repeat center center;
    min-height: 750px;
    background-size: cover;
    padding: 30px 20px;
}

.price__header {
    text-align: center;
    font-size: 36px;
}

.price__cart {
    width: 220px;
    height: 410px;
    background-color: #fff;
}

.price__cart__buy {
    width: 220px;
    height: 510px;
    background-color: #fff;
    text-align: center;
}

.price__cart img {
    width: 100%;
    height: 300px;
}

.price__cart__buy img {
    width: 100%;
    height: 300px;
}

.price__city {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}

.price__chooseButton {
    width: 100px;
    height: 25px;
    margin-left: 5px;
    background-color: white;
    border: 1px solid #000;
    cursor: pointer;
    transition: 0.2s all;
}

.price__active {
    color: #fff;
    background-color: #000;
}

.price__cart__wrapper {
    width: 25%;
    display: flex;
    justify-content: center;
}

.price__cart__wrapper:nth-child(1n + 5) {
    margin-top: 20px;
}

.price__cart {
    text-align: center;
}

.price__price {
    margin-top: 10px;
}

.price__wrapper {
    margin-top: 30px;
    margin-bottom: 20px;
    display: flex;
    justify-content: left;
    align-items: center;
    flex-wrap: wrap;
}

.price__clear {
    font-size: 24px;
    text-align: center;
    margin-top: 30px;
}

.price__showMore {
    text-align: center;
    margin-top: 50px;
}

.price__showMore a {
    color: #000;
    transition: 0.1s all;
}

.price__showMore a:hover {
    color: #0c2763;
    text-decoration: underline;
}

.price__order {
    width: 100px;
    height: 25px;
    background-color: #000;
    border: 1px solid #000;
    cursor: pointer;
    transition: 0.2s all;
    color: #fff;
    margin-top: 5px;
}

.price__counter {
    margin-top: 5px;
    font-size: 18px;
}

.addAndRemove {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 5px;
}

.counterChanger {
    cursor: pointer;
    user-select: none;
}

.removeItem {
    margin-top: 10px;
    width: 100px;
    height: 25px;
    margin-left: 5px;
    background-color: #000;
    border: 1px solid #000;
    cursor: pointer;
    transition: 0.2s all;
    color: #fff;
}

.allPrice {
    text-align: center;
}

.toOrder {
    width: 200px;
    height: 50px;
    background-color: #000;
    border: 1px solid #000;
    cursor: pointer;
    transition: 0.2s all;
    color: #fff;
    font-size: 20px;
}

.toOrder:hover {
    background-color: #fff;
    border: 1px solid #fff;
    color: #000;
}

.button__wrapper {
    text-align: center;
    margin-top: 20px;
}

/*Modal*/

.modal {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.modal__window {
    width: 400px;
    height: 500px;
    background: #fff;
    position: relative;
    text-align: center;
}

.modal .close {
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
}

.modal__window h2 {
    margin-top: 30px;
}

.input__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}

.label__input {
    font-size: 26px;
}

.confirm {
    width: 200px;
    height: 50px;
    background-color: #000;
    border: 1px solid #000;
    cursor: pointer;
    transition: 0.2s all;
    color: #fff;
    font-size: 20px;
    margin-top: 50px;
}

.confirm:hover {
    background-color: #fff;
    border: 1px solid #fff;
    color: #000;
}

.city__price {
    margin-left: 5px;
    font-size: 26px;
}

.city__price__link {
    color: #000;
}

.waterInBottleHeader {
    text-align: center;
    font-size: 40px;
}
