.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
    width: 300px;
    height: 100%;
    z-index: 9999;
}

.textAndIcon {
    display: flex;
    justify-content: center;
    align-items: center;
}

.marginTopForIconAndText {
    background-color: #008ddc;
    padding: 20px 0;
}

.sidebar__ul {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.sidebar__item {
    list-style: none;
}

.sidebar__item a {
    color: #008ddc;
    font-size: 24px;
    text-decoration: none;
    margin-left: 5px;
    transition: 0.2s all;
}

.sidebar__item a:hover {
    color: #000;
}

.sidebar__item:last-child {
    margin-bottom: 50px;
}

.material-icons {
    color: #008ddc;
}

.social__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.instagram {
    width: 30px;
    height: 30px;
    margin-top: 10px;
}

.numberFooter {
    color: #000;
    margin-top: 5px;
}

.footerSidebar {
    background-color: #008ddc;
    width: 100%;
    height: 150px;
    list-style: none;
}

.bottle__image {
    width: 25px;
    height: 34px;
    fill: #008ddc;
}
