@media (max-width: 1285px) {
    .main .manWithBottle {
        width: 400px;
        height: 420px;
    }

    .price__wrapper .price__cart__wrapper {
        width: 33%;
    }

    .price__wrapper .price__cart__wrapper:nth-child(4) {
        margin-top: 15px;
    }
}

@media (max-width: 1100px) {
    .main {
        flex-direction: column-reverse;
    }
    .main .manWithBottle {
        width: 500px;
        height: 520px;
    }
}

@media (max-width: 1040px) {
    .price__wrapper .price__cart__wrapper {
        width: 50%;
    }

    .price__wrapper .price__cart__wrapper:nth-child(4) {
        margin-top: 0;
    }

    .price__wrapper .price__cart__wrapper:nth-child(3),
    .price__wrapper .price__cart__wrapper:nth-child(4) {
        margin-top: 15px;
    }
}

@media (max-width: 1024px) {
    .marginLeft {
        margin-left: 0;
    }

    .header__hamburger {
        display: block;
        position: fixed;
        z-index: 2;
        right: 10px;
        top: 50px;
        width: 26px;
        height: 32px;
    }

    .header__ham1 {
        display: block;
        height: 3px;
        width: 100%;
        background-color: #000;
        margin-bottom: 3px;
        transition: 0.5s all;
    }

    .header__ham2 {
        display: block;
        height: 3px;
        width: 100%;
        background-color: #000;
        margin-bottom: 3px;
        transition: 0.5s all;
    }

    .header__ham3 {
        display: block;
        height: 3px;
        width: 100%;
        background-color: #000;
        transition: 0.5s all;
    }

    .header__rotate1 {
        transform: translateY(3px) rotate(45deg);
    }
    .header__rotate2 {
        display: none;
    }
    .header__rotate3 {
        transform: translateY(-3px) rotate(-45deg);
    }

    .city__img {
        margin-top: 50px;
    }

    .sidebar {
        display: none;
    }
}

@media (max-width: 800px) {
    .city__img {
        width: 100%;
        height: 100%;
        margin-top: 70px;
    }
}

@media (max-width: 750px) {
    .city__ul {
        padding: 10px 0;
    }
}

@media (max-width: 650px) {
    .sidebar {
        width: 250px;
    }
}

@media (max-width: 525px) {
    .price__wrapper .price__cart__wrapper {
        width: 100%;
        margin-top: 15px;
    }

    .main h1 {
        text-align: center;
        font-size: 44px;
    }
}

@media (max-width: 475px) {
    .main .manWithBottle {
        width: 100%;
        height: 300px;
        margin-right: 0;
    }

    .main h1 {
        text-align: center;
        font-size: 38px;
    }

    .main {
        min-height: 0;
    }
}

@media (max-width: 370px) {
    .main .manWithBottle {
        width: 100%;
        height: 220px;
        margin-right: 0;
    }
}
