.sidebarMap {
    height: 100%;
    width: 0;
    visibility: hidden;
    position: fixed;
    right: 0;
    top: 0;
    background: rgba(255, 255, 255, 0.5);
    text-align: center;
    transition: 0.2s width;
}

.closeSidebar {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 50px;
    color: #000;
    cursor: pointer;
}

.city__img {
    width: 300px;
    height: 300px;
    margin-top: 120px;
}

.city__img__flex {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 60px;
}

.sidebarMap__header {
    margin-top: 20px;
    font-size: 36px;
}

.city__ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px 80px;
    list-style: none;
}

.city__ul li a {
    color: #000;
    cursor: pointer;
    text-decoration: none;
    font-size: 24px;
}

.city__ul li a:hover {
    color: #0c2763;
}

.showMap {
    width: 40%;
    visibility: visible;
}

.showMapTablet {
    width: 60%;
    visibility: visible;
}

.city__ul {
    padding: 10px 40px;
}
