.about {
    position: relative;
    z-index: -2;
}

.about:after {
    content: '';
    background: url('../../images/backgroundForText.JPG') no-repeat center
        center;
    background-size: cover;
    opacity: 0.3;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
}

.about__text {
    font-size: 21px;
    color: #000;
    padding: 10px;
    padding-top: 0;
    font-family: 'Roboto', sans-serif;
}

.about__text__header {
    text-align: center;
    padding-top: 18px;
    font-size: 36px;
}

.water__descr {
    width: 800px;
    height: 600px;
}

.water__img__bottle {
    width: 100%;
    text-align: center;
    z-index: -1;
}
